/**
 * Enhanced database utility functions
 * Provides robust database connection and query methods
 */

import { Pool } from 'pg';
import { handleDbError } from './error-utils';

// Create a connection pool
const pool = new Pool({
  connectionString: process.env.DATABASE_URL,
  ssl: process.env.NODE_ENV === 'production' ? { rejectUnauthorized: false } : false,
  max: 20, // Maximum number of clients in the pool
  idleTimeoutMillis: 30000, // How long a client is allowed to remain idle before being closed
  connectionTimeoutMillis: 2000, // How long to wait for a connection to become available
});

// Log connection events
pool.on('connect', () => {
  console.log('Database connection established');
});

pool.on('error', (err) => {
  console.error('Unexpected database error:', err);
});

/**
 * Enhanced database interface with error handling and connection management
 */
export const db = {
  /**
   * Execute a database query with error handling
   * @param {string} text - SQL query text
   * @param {Array} params - Query parameters
   * @returns {Promise<Object>} - Query result
   */
  async query(text, params = []) {
    const client = await pool.connect();
    try {
      const start = Date.now();
      const result = await client.query(text, params);
      const duration = Date.now() - start;
      
      // Log slow queries for performance monitoring
      if (duration > 100) {
        console.log('Slow query:', { text, duration, rows: result.rowCount });
      }
      
      return result;
    } catch (error) {
      handleDbError(error);
    } finally {
      client.release();
    }
  },
  
  /**
   * Execute a transaction with multiple queries
   * @param {Function} callback - Function that receives a client and executes queries
   * @returns {Promise<any>} - Transaction result
   */
  async transaction(callback) {
    const client = await pool.connect();
    try {
      await client.query('BEGIN');
      const result = await callback(client);
      await client.query('COMMIT');
      return result;
    } catch (error) {
      await client.query('ROLLBACK');
      handleDbError(error);
    } finally {
      client.release();
    }
  },
  
  /**
   * Insert a record into a table
   * @param {string} table - Table name
   * @param {Object} data - Record data
   * @returns {Promise<Object>} - Inserted record
   */
  async insert(table, data) {
    const keys = Object.keys(data);
    const values = Object.values(data);
    const placeholders = keys.map((_, i) => `$${i + 1}`).join(', ');
    const columns = keys.join(', ');
    
    const query = `
      INSERT INTO ${table} (${columns})
      VALUES (${placeholders})
      RETURNING *
    `;
    
    const result = await this.query(query, values);
    return result.rows[0];
  },
  
  /**
   * Update a record in a table
   * @param {string} table - Table name
   * @param {string} id - Record ID
   * @param {Object} data - Updated data
   * @returns {Promise<Object>} - Updated record
   */
  async update(table, id, data) {
    const keys = Object.keys(data);
    const values = Object.values(data);
    const setClause = keys.map((key, i) => `${key} = $${i + 1}`).join(', ');
    
    const query = `
      UPDATE ${table}
      SET ${setClause}, updated_at = CURRENT_TIMESTAMP
      WHERE id = $${keys.length + 1}
      RETURNING *
    `;
    
    const result = await this.query(query, [...values, id]);
    return result.rows[0];
  },
  
  /**
   * Find records with pagination
   * @param {string} table - Table name
   * @param {Object} where - Where conditions
   * @param {Object} options - Query options
   * @returns {Promise<Object>} - Records and pagination info
   */
  async findWithPagination(table, where = {}, options = {}) {
    const { 
      page = 1, 
      limit = 20, 
      orderBy = 'created_at', 
      order = 'DESC',
      fields = '*'
    } = options;
    
    const offset = (page - 1) * limit;
    const whereKeys = Object.keys(where);
    const whereValues = Object.values(where);
    
    let whereClause = '';
    if (whereKeys.length > 0) {
      whereClause = 'WHERE ' + whereKeys.map((key, i) => {
        if (whereValues[i] === null) {
          return `${key} IS NULL`;
        }
        return `${key} = $${i + 1}`;
      }).join(' AND ');
    }
    
    // Count total records
    const countQuery = `
      SELECT COUNT(*) as total
      FROM ${table}
      ${whereClause}
    `;
    
    // Get paginated records
    const dataQuery = `
      SELECT ${fields}
      FROM ${table}
      ${whereClause}
      ORDER BY ${orderBy} ${order}
      LIMIT ${limit} OFFSET ${offset}
    `;
    
    const [countResult, dataResult] = await Promise.all([
      this.query(countQuery, whereValues),
      this.query(dataQuery, whereValues)
    ]);
    
    const total = parseInt(countResult.rows[0].total);
    
    return {
      data: dataResult.rows,
      pagination: {
        page: Number(page),
        limit: Number(limit),
        total,
        pages: Math.ceil(total / limit)
      }
    };
  },
  
  /**
   * Close the database connection pool
   * @returns {Promise<void>}
   */
  async end() {
    await pool.end();
    console.log('Database connection pool has ended');
  }
};
